import { Component } from 'react';
class LighthouseReport extends Component {
  constructor(props) {
    super(props);

    console.log('lighthouseReport', props);
    this.state = {
      performance: Math.round(props.data.categories.performance.score * 100),
      seo: Math.round(props.data.categories.seo.score * 100),
      accessibility: Math.round(props.data.categories.accessibility.score * 100),
      pwa: Math.round(props.data.categories.pwa.score * 100),
      'best-practices': Math.round(props.data.categories['best-practices'].score * 100)
    };
  }
  componentDidMount() {
  }
  render() {
    return (
      <div>
        <h5 className="title is-5">Ligthouse</h5>
        Performance score: { this.state.performance }<br></br>
        SEO score: { this.state.seo }<br></br>
        Accessibility score: { this.state.accessibility }<br></br>
        Best practices score: { this.state['best-practices'] }<br></br>
        PWA score: { this.state.pwa }<br></br>
        <a href={this.props.lighthouseReportURL} target="_blank">View complete report</a>
      </div>

    );
  }
}
export default LighthouseReport;